




















































import Vue from "vue";
export default Vue.extend({
  data() {
    return {
      message: "",
    };
  },
  created() {
    if (this.$route.params.type == "403" || this.$route.params.type == "401") {
      this.message = " .";
    } else if (this.$route.params.type == "404") {
      this.message = "  .";
    } else if (this.$route.params.type == "500") {
      this.message =
        " .  . \n010-5152-9445";
    }
  },
  methods: {
    goHome() {
      this.$router.push("/");
    },
  },
});
